.vue-scroll-picker {
  position: relative;
  width: 100%;
  height: 10em;
  overflow: hidden; }

.vue-scroll-picker-list {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }

.vue-scroll-picker-list-rotator {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  padding-top: 4.4em; }
  .vue-scroll-picker-list-rotator.-transition {
    transition: top ease 200ms; }

.vue-scroll-picker-item {
  position: relative;
  text-align: center;
  height: 1.2em;
  line-height: 1.2em; }
  .vue-scroll-picker-item.-placeholder {
    color: #aaa; }
  .vue-scroll-picker-item label {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateX(0);
    transition: transform .3s; }
  .vue-scroll-picker-item.prev label {
    transform: translate(-50%, -50%) rotateX(-60deg); }
  .vue-scroll-picker-item.prev-2 label {
    transform: translate(-50%, -50%) rotateX(-50deg); }
  .vue-scroll-picker-item.prev-1 label {
    transform: translate(-50%, -50%) rotateX(-40deg); }
  .vue-scroll-picker-item.next-1 label {
    transform: translate(-50%, -50%) rotateX(40deg); }
  .vue-scroll-picker-item.next-2 label {
    transform: translate(-50%, -50%) rotateX(50deg); }
  .vue-scroll-picker-item.next label {
    transform: translate(-50%, -50%) rotateX(60deg); }
  .vue-scroll-picker-item.-selected label, .vue-scroll-picker-item.dragging label {
    transform: translate(-50%, -50%); }

.vue-scroll-picker-layer {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0; }
  .vue-scroll-picker-layer .top,
  .vue-scroll-picker-layer .middle,
  .vue-scroll-picker-layer .bottom {
    position: absolute; }
  .vue-scroll-picker-layer .top {
    box-sizing: border-box;
    border-bottom: 1px solid #c8c7cc;
    background: linear-gradient(180deg, #fff 10%, rgba(255, 255, 255, 0.7));
    top: 0;
    left: 0;
    right: 0;
    height: 40%;
    cursor: pointer; }
  .vue-scroll-picker-layer .middle {
    top: 40%;
    left: 0;
    right: 0;
    bottom: 40%; }
  .vue-scroll-picker-layer .bottom {
    border-top: 1px solid #c8c7cc;
    background: linear-gradient(0deg, #fff 10%, rgba(255, 255, 255, 0.7));
    bottom: 0;
    left: 0;
    right: 0;
    height: 40%;
    cursor: pointer; }
